	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>2017.7</p>\n</blockquote>\n<h5 id=\"self-made-experimental-electrical-instruments-combining-percussion-with-continuous-synth\"><a href=\"#self-made-experimental-electrical-instruments-combining-percussion-with-continuous-synth\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Self-made experimental electrical instruments, combining percussion with continuous synth.</h5>\n<br>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <iframe src=\"https://player.vimeo.com/video/240780843\" frameborder=\"0\" webkitallowfullscreen=\"\" mozallowfullscreen=\"\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n<p><a href=\"https://vimeo.com/240780843\">WhipWhip | Self-Made Musical Instrument with Optic Fiber</a> from <a href=\"https://vimeo.com/user56788864\">Vibert Thio</a> on <a href=\"https://vimeo.com\">Vimeo</a>.</p>\n          </div>\n          </div>\n<h2 id=\"about\"><a href=\"#about\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>About</h2>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 35.89620374819798%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 05\"\n        title=\"\"\n        src=\"/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-f688c.jpg\"\n        srcset=\"/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-08150.jpg 148w,\n/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-bdd1b.jpg 295w,\n/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-f688c.jpg 590w,\n/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-7989c.jpg 885w,\n/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-52e85.jpg 1180w,\n/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-35085.jpg 1770w,\n/portfolio/static/img-05-83f19bc1b70fca4f7e13f0bc060e090f-1f9ed.jpg 4162w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n<em>Iang Tu and I used “WhipWhip” in our live set. (2017.9)</em></p>\n<br>\n<p>Inspired by the artist Onyx Ashanti, who exploring a new path of performance, I want to make an instrument which can detect my gesture and be the lighting device at the same time. I use Arduino as the MCU, optical fiber as light, 6-axis gyroscope as sensor, and Bluetooth protocol to communicate.</p>\n<br>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 69.11268038071844%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 01\"\n        title=\"\"\n        src=\"/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-f688c.jpg\"\n        srcset=\"/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-08150.jpg 148w,\n/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-bdd1b.jpg 295w,\n/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-f688c.jpg 590w,\n/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-7989c.jpg 885w,\n/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-52e85.jpg 1180w,\n/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-35085.jpg 1770w,\n/portfolio/static/img-01-7591c261a7aabb22de5d9295a1ad744e-eff71.jpg 3257w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n<em>The prototype of WhipWhip on bread board. (2017.7)</em></p>\n<br>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 42.263279445727484%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 02\"\n        title=\"\"\n        src=\"/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-f688c.jpg\"\n        srcset=\"/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-08150.jpg 148w,\n/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-bdd1b.jpg 295w,\n/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-f688c.jpg 590w,\n/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-7989c.jpg 885w,\n/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-52e85.jpg 1180w,\n/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-35085.jpg 1770w,\n/portfolio/static/img-02-ead595fa4f54a92f934d6f7bafd2f90d-c97fb.jpg 4330w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 379px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 50.44359949302915%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 03\"\n        title=\"\"\n        src=\"/portfolio/static/img-03-7a026eb5cc536466ab99ae36e3f5d457-44c1f.png\"\n        srcset=\"/portfolio/static/img-03-7a026eb5cc536466ab99ae36e3f5d457-ee3e7.png 148w,\n/portfolio/static/img-03-7a026eb5cc536466ab99ae36e3f5d457-d2efb.png 295w,\n/portfolio/static/img-03-7a026eb5cc536466ab99ae36e3f5d457-44c1f.png 590w,\n/portfolio/static/img-03-7a026eb5cc536466ab99ae36e3f5d457-c1288.png 789w\"\n        sizes=\"(max-width: 379px) 100vw, 379px\"\n      />\n    </span>\n  </span>\n  \n<em>The final version on bakelite board. (2017.8)</em></p>\n<br>\n<h2 id=\"links\"><a href=\"#links\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Links</h2>\n<p><a href=\"https://soundcloud.com/duanger\">> Iang Tu</a></p>\n<br>","frontmatter":{"title":"WhipWhip"}}},"pathContext":{"slug":"/projects/whipwhip/"}}

